* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;

}

::-webkit-scrollbar {
    width: 8px;
    height: 2px;
}

::-webkit-scrollbar-track {
    background-color: #dedede;
}

::-webkit-scrollbar-thumb {

    background-color: #EF4747;
    border-radius: 8px;
}

::-webkit-scrollbar-thumb:hover {
    background: #EF4747;
}